import {
    ColorType,
    createChart as createLightWeightChart,
    CrosshairMode,
    ISeriesApi,
    UTCTimestamp
} from "lightweight-charts";

export class ChartManager{
    private candleSeries: ISeriesApi<"Candlestick">;
    private lastUpdatTime: number = 0;
    private chart: any;
    private currentBar: {
        open: number | null;
        high: number | null;
        low: number | null;
        close: number | null;
    } = {
        open: null,
        high: null,
        low: null,
        close: null
    };

    constructor(
        ref: any,
        initialData: any[],
        layout: {background: string; color: string}
    ){
        const chart = createLightWeightChart(ref, {
            autoSize: true,
            overlayPriceScales: {
                ticksVisible: true,
                borderVisible: true
            },
            crosshair: {
                mode: CrosshairMode.Normal
            },
            rightPriceScale: {
                visible: true,
                ticksVisible: true,
                entireTextOnly: true
            },
            grid: {
                horzLines: {
                    visible: false
                },
            },
            layout: {
                background: {
                    type: ColorType.Solid,
                    color: layout.background,
                },
                textColor: "white"
            },
        });
        this.chart = chart;
        this.candleSeries = chart.addCandlestickSeries();

        this.candleSeries.setData(
            initialData.map((data) => ({
                ...data,
                time: (data.timestamp/1000) as UTCTimestamp,
            }))
        )
    }

    public update(updatePrice: any){
        if(!this.lastUpdatTime)
        {
            this.lastUpdatTime = new Date().getTime();
        }

        this.candleSeries.update({
            time: (this.lastUpdatTime / 1000) as UTCTimestamp,
            close: updatePrice.close,
            low: updatePrice.low,
            high: updatePrice.high,
            open: updatePrice.open
        });

        if(updatePrice.newCandleInitiated)
        {
            this.lastUpdatTime = updatePrice.time;
        }
    }
    public destroy()
    {
        this.chart.remove();
    }
}
